.home {
  overflow: hidden;
  &__header {
    width: 100%;
    position: absolute;
    left: 0;
    top: 0;
    height: 154px;
    background: rgba(0,0,0,0.6) url('../images/home-header-bg.png') repeat-x center top;
    background-size: auto 100%;
    z-index: 100;
    @include susy-breakpoint($s-320, $c8) {
      height: 182px;
      background-image: url('../images/home-header-bg-m.png');
      background-size: auto 100%;
    }
    @include susy-breakpoint($s-768, $c12) {
      height: 130px;
    }
  }
  &__inner {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
    @include susy-breakpoint($s-320, $c8) {
      width: 320px;
      flex-direction: column;
      justify-content: center;
    }
    @include susy-breakpoint($s-768, $c12) {
      width: 720px;
    }
    @include susy-breakpoint($s-960, $c12) {
      width: 960px;
    }
  }
  &__logo {
    width: 685px;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
    @include susy-breakpoint($s-320, $c8) {
      width: 320px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
    }
    @include susy-breakpoint($s-768, $c12) {
      width: 400px;
    }
  }
  &__nav {
    position: relative;
    display: flex;
    @include susy-breakpoint($s-320, $c8) {
      width: 100%;
      justify-content: space-between;
    }
  }
  &__btn {
    width: 223px;
    display: block;
    margin: 0 8px;
    position: relative;
    @include susy-breakpoint($s-768, $c12) {
      width: 160px;
    }
    img {
      &.bg {
        max-width: 100%;
        display: block;
        margin: 0;
        position: relative;
        z-index: 0;
        transition: all 300ms ease-in-out;
      }
      &.hover {
        max-width: 100%;
        display: block;
        margin: 0;
        left: 0;
        top: 0;
        position: absolute;
        z-index: 10;
        opacity: 0;
        transition: all 300ms ease-in-out;
      }
    }
    &:hover {
      img {
        &.bg {
          opacity: 0;
        }
        &.hover {
          opacity: 1;
        }
      }
    }
  }
  &__video {
    @include susy-breakpoint($s-320, $c8) {
      height: 100vh;
      position: relative;
      width: 100%;
    }
    video {
      width: 100%;
      height: auto;
      display: block;
      margin: 0;
      @include susy-breakpoint($s-320, $c8) {
        position: absolute;
        width: auto;
        height: 100%;
        left: 50%;
        top: 0;
        transform: translate3d(-50%,0,0);
      }
    }
  }
  &__btn-sound-toggle {
    background: transparent url('../images/sound-control-bg.png') no-repeat 0 0;
    background-size: 71px auto;
    width: 71px;
    height: 71px;
    position: absolute;
    z-index: 50;
    top: 160px;
    left: 50%;
    transform: translate3d(730%,0,0);
    border: 0;
    &.paused {
      background-position-y: -71px;
    }
    @include susy-breakpoint($s-320, $c8) {
      left: inherit;
      right: 16px;
      top: 200px;
      transform: translate3d(0,0,0);
    }
    @include susy-breakpoint($s-768, $c12) {
      transform: translate3d(390%,0,0);
      top: 140px;
    }
    @include susy-breakpoint($s-960, $c12) {
      transform: translate3d(560%,0,0);
    }
  }
  &__mobile {
    display: none;
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
}


.home-light-box {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  z-index: 1200;
  pointer-events: none;
  &.active {
    opacity: 1;
    pointer-events: auto;
  }
  &__overlay {
    background: rgba(0,0,0,0.9);
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn-close {
    position: absolute;
    width: 23px;
    height: 23px;
    right: 0;
    top: -30px;
    background: transparent url('../images/home-btn-close@2x.png') no-repeat center center;
    border: 0;
    padding: 0;
  }
  &__inner {
    position: relative;
    width: 416px;
    height: 212px;
    @include susy-breakpoint($s-320, $c8) {
      width: 416px * 0.8;
      height: 212px * 0.8;
    }
    img {
      max-width: 100%;
      display: block;
      margin: 0;
    }
  }
}
